import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';
import { hexColorToRgb, rgbToString, withMediaQuery } from 'util/styleHelpers';
import withWindowDimensions from 'util/withWindowDimensions';

import {
  NAVBAR_HEIGHT_DESKTOP,
  NAVBAR_HEIGHT_MOBILE,
  FONT_HEADER_BOLD,
  FONT_HEADER_NORMAL,
} from 'styles/theme';

import { landingHeaderBaseStyle } from 'styles/groups';

import sun from './sun.svg';
import cloud from './cloud.svg';

let TweenLite = {};
if (process.env.BROWSER) {
  TweenLite = require('gsap/TweenLite'); // eslint-disable-line global-require
  require('gsap/CSSPlugin'); // eslint-disable-line global-require
}

/**
 * Constants
 */

const SUN_START = 25;
const skyColorStart = '#acf8fc';
const skyColorEnd = '#ff9186';
const SKY_COLOR_START = hexColorToRgb(skyColorStart);
const SKY_COLOR_END = hexColorToRgb(skyColorEnd);
const SUN_SIZE_DESKTOP = '110px';
const SUN_SIZE_MOBILE = '100px';
const cloudWidthMobile = 100;
const CLOUD_WIDTH_MOBILE = `${cloudWidthMobile}px`;
const CLOUD_HEIGHT_MOBILE = `${cloudWidthMobile * (100 / 131)}px`;
const cloudWidthDesktop = 110;
const CLOUD_WIDTH_DESKTOP = `${cloudWidthDesktop}px`;
const CLOUD_HEIGHT_DESKTOP = `${cloudWidthDesktop * (100 / 131)}px`;
const NUM_WAVES = 5;
const WAVE_BASE_OFFSET = 40;
const WAVE_OFFSET = 10;
const waveHeightMobile = 100;
const WAVE_HEIGHT_MOBILE = `${waveHeightMobile}px`;
const WAVE_WIDTH_MOBILE = `${waveHeightMobile * (89 / 102.1)}px`;
const waveHeightDesktop = 140;
const WAVE_HEIGHT_DESKTOP = `${waveHeightDesktop}px`;
const WAVE_WIDTH_DESKTOP = `${waveHeightDesktop * (89 / 102.1)}px`;
const BEACH_COLOR = '#eed6af';

const CLOUD_OFFSET = 50;
const CLOUD_PIXELS_PER_SECOND = 20;
const WAVE_DIVISOR_X = 12;
const WAVE_DIVISOR_Y = 36;
const WAVE_PIXELS_PER_SECOND = 15;

let zIndex = 0;


/**
 * Styled Components
 */

const LandingContainer = styled('section', {
  width: '100%',
  height: '100%',
  backgroundColor: skyColorStart,
});

const HeaderContainer = styled('header', {
  width: '100%',
  position: 'absolute',
  top: '25%',
  zIndex: 9998,
  transform: 'translateY(-50%)',
  textAlign: 'center',
});

const H1 = styled('h1', withMediaQuery(landingHeaderBaseStyle(FONT_HEADER_BOLD), {
  fontSize: '2.5em',
}, {
  fontSize: '3.5em',
}));

const H3 = styled('h3', withMediaQuery(landingHeaderBaseStyle(FONT_HEADER_NORMAL), {
  fontSize: '1.1em',
}, {
  fontSize: '1.5em',
}));

const H4 = styled('h4', withMediaQuery(landingHeaderBaseStyle(FONT_HEADER_NORMAL), {
  fontSize: '1em',
}, {
  fontSize: '1.25em',
}));

const Inner = styled('div', withMediaQuery({
  width: '100%',
  position: 'absolute',
  overflow: 'hidden',
  bottom: 0,
}, {
  top: NAVBAR_HEIGHT_MOBILE,
  marginBottom: NAVBAR_HEIGHT_MOBILE,
}, {
  top: NAVBAR_HEIGHT_DESKTOP,
  marginBottom: NAVBAR_HEIGHT_DESKTOP,
}));

const LandingProp = styled(({ className, style = {}, componentRef = () => {}, children }) => (
  <div className={className} style={style} ref={componentRef}>
    {children}
  </div>
), {
  position: 'absolute',
});

const LandingPropSunCloud = styled(LandingProp, {
  backgroundPosition: 'center',
  backgroundSize: '100% 100%',
  backgroundRepeat: 'no-repeat',
});

const Sun = styled(LandingPropSunCloud, withMediaQuery({
  zIndex: (zIndex += 1),
  backgroundImage: `url(${sun})`,
}, {
  width: SUN_SIZE_MOBILE,
  height: SUN_SIZE_MOBILE,
  top: `${SUN_START}px`,
  right: `${SUN_START}px`,
}, {
  width: SUN_SIZE_DESKTOP,
  height: SUN_SIZE_DESKTOP,
  top: `${SUN_START}px`,
  right: `${SUN_START}px`,
}));

const Cloud = styled(LandingPropSunCloud, withMediaQuery({
  zIndex: (zIndex += 1),
  backgroundImage: `url(${cloud})`,
}, {
  width: CLOUD_WIDTH_MOBILE,
  height: CLOUD_HEIGHT_MOBILE,
}, {
  width: CLOUD_WIDTH_DESKTOP,
  height: CLOUD_HEIGHT_DESKTOP,
}));

const Clouds = [
  {
    key: 'cloud1',
    Component: styled(Cloud, {
      top: '10%',
      left: '5%',
    }),
  },
  {
    key: 'cloud2',
    Component: styled(Cloud, {
      top: '15%',
      right: '10%',
    }),
  },
];

const Wave = styled(LandingProp, withMediaQuery({
  width: '200%',
}, {
  height: WAVE_HEIGHT_MOBILE,
  left: `-${WAVE_WIDTH_MOBILE}`,
}, {
  height: WAVE_HEIGHT_DESKTOP,
  left: `-${WAVE_WIDTH_DESKTOP}`,
}));

// eslint-disable-next-line no-return-assign
const Waves = Array.apply(0, Array(NUM_WAVES)).map((_, i) => i + 1).map(i => ({
  key: `wave${i}`,
  Component: styled(Wave, {
    zIndex: (zIndex += 1),
    backgroundImage: `url(${require(`./wave${i}.svg`)})`, // eslint-disable-line global-require, import/no-dynamic-require
    top: `${WAVE_BASE_OFFSET + (i * WAVE_OFFSET)}%`,
    backgroundPosition: (i % 2 === 0) ? 'initial' : '40px 0',
  }),
}));

const Beach = styled('div', withMediaQuery({
  width: '100%',
  backgroundColor: BEACH_COLOR,
  position: 'absolute',
  bottom: 0,
}, {
  height: NAVBAR_HEIGHT_MOBILE,
}, {
  height: NAVBAR_HEIGHT_DESKTOP,
}));


/**
 * Landing
 */

const propTypes = {
  windowWidth: PropTypes.number.isRequired,
  windowHeight: PropTypes.number.isRequired,
};

const contextTypes = {};
const defaultProps = {};

class Landing extends React.PureComponent {
  static getRandomValue(max) {
    return (Math.random() * max) - (max / 2);
  }

  static getRandomCoordinate(xMax, yMax) {
    const x = Landing.getRandomValue(xMax);
    const y = Landing.getRandomValue(yMax);

    return { x, y };
  }

  static animate(el, duration, x, y, ease, onComplete) {
    TweenLite.to(el, duration, { css: { x, y }, ease, onComplete });
  }

  static calculateLandingBackgroundColor(scrollPercent) {
    const backgroundColor = { r: 0, g: 0, b: 0 };

    Object.keys(backgroundColor).forEach((key) => {
      backgroundColor[key] = ((SKY_COLOR_START[key] * (1 - scrollPercent)) + (SKY_COLOR_END[key] * scrollPercent)).toFixed();
    });

    return rgbToString(backgroundColor);
  }

  constructor(props) {
    super(props);

    const { windowWidth, windowHeight } = props;

    this.state = {
      sunGoalX: windowWidth * (3 / 4),
      sunGoalY: windowHeight / 2,
      scrollPercent: 0,
    };

    this._onWindowScroll = this.onWindowScroll.bind(this);
    this._animateClouds = this.animateClouds.bind(this);
    this._animateWaves = this.animateWaves.bind(this);
  }

  componentDidMount() {
    this._animateClouds();
    this._animateWaves();

    this._onWindowScroll();
    window.addEventListener('scroll', this._onWindowScroll);
  }

  componentWillUnmount() {
    this.stopAnimations = true;

    window.removeEventListener('scroll', this._onWindowScroll);
  }

  onWindowScroll() {
    const { sunGoalY, scrollPercent } = this.state;
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

    let newScrollPercent = 0;
    if (sunGoalY === 0) {
      newScrollPercent = 0;
    } else if (scrollTop > sunGoalY) {
      newScrollPercent = 1;
    } else {
      newScrollPercent = scrollTop / sunGoalY;
    }

    if (scrollPercent !== newScrollPercent) {
      this.setState({ scrollPercent: newScrollPercent });
    }
  }

  animateClouds() {
    const animateCloud = el => (prevX = 0, prevY = 0) => {
      if (!this.stopAnimations) {
        const { x, y } = Landing.getRandomCoordinate(CLOUD_OFFSET, CLOUD_OFFSET);
        const duration = Math.sqrt((Math.abs(prevX - x) ** 2) + (Math.abs(prevY - y) ** 2)) / CLOUD_PIXELS_PER_SECOND;
        const onComplete = animateCloud(el).bind(null, x, y);
        const ease = TweenLite.Power0.easeNone;

        Landing.animate(el, duration, x, y, ease, onComplete);
      }
    };

    Clouds.forEach(({ key }) => animateCloud(this[key])());
  }

  animateWaves() {
    const animateWave = el => (prevX = 0, prevY = 0) => {
      if (!this.stopAnimations) {
        const { windowWidth, windowHeight } = this.props;

        const { x, y } = Landing.getRandomCoordinate(windowWidth / WAVE_DIVISOR_X, windowHeight / WAVE_DIVISOR_Y);
        const duration = Math.sqrt((Math.abs(prevX - x) ** 2) + (Math.abs(prevY - y) ** 2)) / WAVE_PIXELS_PER_SECOND;
        const onComplete = animateWave(el).bind(null, x, y);
        const ease = TweenLite.Power0.easeNone;

        Landing.animate(el, duration, x, y, ease, onComplete);
      }
    };

    Waves.forEach(({ key }) => animateWave(this[key])());
  }

  render() {
    const { sunGoalX, sunGoalY, scrollPercent } = this.state;

    const landingBackgroundColor = Landing.calculateLandingBackgroundColor(scrollPercent);

    const sunAngle = (1 + scrollPercent) * (Math.PI / 2);
    const sunTop = `${(sunGoalY - (Math.sin(sunAngle) * (sunGoalY - SUN_START))).toFixed()}px`;
    const sunRight = `${(SUN_START - (Math.cos(sunAngle) * (sunGoalX - SUN_START))).toFixed()}px`;

    return (
      <LandingContainer style={{ backgroundColor: landingBackgroundColor }}>
        <HeaderContainer>
          <H1>David Luu</H1>
          <H3>Software Engineer, Web Developer</H3>
          <H4>Software Development Engineer, Amazon</H4>
        </HeaderContainer>
        <Inner>
          <Sun style={{ top: sunTop, right: sunRight }} />
          {Clouds.map(({ key, Component }) =>
            <Component key={key} componentRef={(el) => { this[key] = el; }} />
          )}
          {Waves.map(({ key, Component }) =>
            <Component key={key} componentRef={(el) => { this[key] = el; }} />
          )}
        </Inner>
        <Beach />
      </LandingContainer>
    );
  }
}

Landing.propTypes = propTypes;
Landing.contextTypes = contextTypes;
Landing.defaultProps = defaultProps;

export default withWindowDimensions(Landing);



