import React from 'react';
import PropTypes from 'prop-types';

const propTypes = {
  className: PropTypes.string.isRequired,
  componentRef: PropTypes.func.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(React.PropTypes.node),
    PropTypes.node,
  ]).isRequired,
};

const contextTypes = {};
const defaultProps = {};

const RootContainer = ({ className, componentRef, children }) => (
  <section className={className} ref={componentRef}>
    {children}
  </section>
);

RootContainer.propTypes = propTypes;
RootContainer.contextTypes = contextTypes;
RootContainer.defaultProps = defaultProps;

export default RootContainer;



