import React from 'react';

const moduleDefaultExport = module => module.default || module;

export default function asyncRoute(getComponent, componentName) {
  return class AsyncRoute extends React.PureComponent {
    static Component = null

    constructor(props) {
      super(props);

      this.state = { Component: AsyncRoute.Component };
    }

    componentWillMount() {
      if (process.env.BROWSER && !this.state.Component) {
        getComponent()
          .then(moduleDefaultExport)
          .then((Component) => {
            AsyncRoute.Component = Component;

            if (this._mounted) {
              this.setState({ Component });
            } else {
              this.state.Component = Component;
            }
          });
      }
    }

    componentDidMount() {
      this._mounted = true;
    }

    render() {
      let Component;
      if (process.env.BROWSER) {
        Component = this.state.Component;
      } else {
        Component = require(`modules/${componentName}/${componentName}`).default; // eslint-disable-line global-require, import/no-dynamic-require
      }

      return Component ? <Component {...this.props} /> : null;
    }
  };
}



