/* eslint-disable max-len */

import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';
import { withCollectRef } from 'util/RefCollector';
import { withMediaQuery } from 'util/styleHelpers';

import RootContainer from 'components/RootContainer/RootContainer';

import {
  FONT_BODY_NORMAL,
  THEME_COLOR_2,
  BACKGROUND_COLOR_1,
  COMPONENT_WIDTH_MOBILE,
  COMPONENT_WIDTH_DESKTOP,
} from 'styles/theme';

import { containerStyle } from 'styles/groups';


/**
 * Constants
 */


/**
 * Styled Components
 */

const AboutContainer = styled(RootContainer, containerStyle(BACKGROUND_COLOR_1, 'left'));

const AboutSection = styled('section', withMediaQuery({
  margin: '0 auto',
}, {
  width: COMPONENT_WIDTH_MOBILE,
}, {
  width: COMPONENT_WIDTH_DESKTOP,
}));

const Divider = styled('div', {
  ':after': {
    content: '"\\2022\\2022\\2022\\2022\\2022"',
    margin: '3em 0 2em',
    display: 'block',
    fontSize: '.8em',
    textAlign: 'center',
    letterSpacing: '.5em',
    color: '#ccc',
  },
});

const IntroHeader = styled('h2', {
  margin: '0 0 0.5em',
  ...FONT_BODY_NORMAL,
  fontSize: '1.25em',
  color: THEME_COLOR_2,
});

const IntroText = styled('p', {
  margin: 0,
  ':not(:last-child)': {
    marginBottom: '0.5em',
  },
  fontSize: '1.1em',
});

const AboutSummarySection = styled('div', withMediaQuery({
  margin: '0 auto',
}, {
  width: COMPONENT_WIDTH_MOBILE,
}, {
  width: COMPONENT_WIDTH_DESKTOP,
}));

const AboutSummaryHeader = styled('h3', {
  margin: '0 0 0.75em',
  ...FONT_BODY_NORMAL,
  fontSize: '1.25em',
  color: THEME_COLOR_2,
});

const AboutSummaryItem = styled('div', {
  marginLeft: '2em',
  ':not(:last-child)': {
    marginBottom: '0.25em',
  },
});

const ItemLink = styled('a', {
  color: 'inherit',
  ':hover': {
    color: THEME_COLOR_2,
  },
});

const ItemMinor = styled('span', {
  marginLeft: '1em',
  fontSize: '0.9em',
});

const AboutOutroSection = styled(AboutSection, {
  fontSize: '1.1em',
});

const ExternalLink = styled('a', {
  color: '#000',
  ':hover': {
    color: THEME_COLOR_2,
  },
});


/**
 * About
 */

const propTypes = {
  componentRef: PropTypes.func.isRequired,
};

const contextTypes = {};
const defaultProps = {};

const About = ({ componentRef }) => (
  <AboutContainer componentRef={componentRef}>
    <AboutSection>
      <IntroHeader>Hello World!</IntroHeader>
      <IntroText>My name is David and I am a software engineer and a web developer. I am currently a Software Development Engineer at Amazon.</IntroText>
      <IntroText>I have a passion for coding and problem solving. I am broadly interested in web development and computer security. In my spare time, I like swimming, cooking, and eating food.</IntroText>
    </AboutSection>
    <Divider />
    <AboutSummarySection>
      <AboutSummaryHeader>Work</AboutSummaryHeader>
      <AboutSummaryItem>
        <ItemLink href="https://www.amazon.com/" target="_blank" rel="noopener noreferrer">Amazon</ItemLink> | Software Development Engineer - August 2017 to Present
      </AboutSummaryItem>
      <AboutSummaryItem>
        <ItemLink href="https://cse.ucsd.edu/undergraduate/student-opportunities/financial-opportunities/readers-and-tutors" target="_blank" rel="noopener noreferrer">UCSD CSE Department</ItemLink> | Tutor - September 2015 to June 2017
        <br />
        <ItemMinor>(<ItemLink href="https://cseweb.ucsd.edu/classes/fa15/cse105-a/" target="_blank" rel="noopener noreferrer">FA15</ItemLink>, <ItemLink href="https://cseweb.ucsd.edu/classes/sp16/cse105-ab/" target="_blank" rel="noopener noreferrer">SP16</ItemLink>, <ItemLink href="https://cseweb.ucsd.edu/classes/fa16/cse105-abc/" target="_blank" rel="noopener noreferrer">FA16</ItemLink>, <ItemLink href="https://cseweb.ucsd.edu/classes/wi17/cse20-ab/" target="_blank" rel="noopener noreferrer">WI17</ItemLink>, <ItemLink href="http://cseweb.ucsd.edu/classes/sp17/cse105-ab/" target="_blank" rel="noopener noreferrer">SP17</ItemLink>)</ItemMinor>
      </AboutSummaryItem>
    </AboutSummarySection>
    <Divider />
    <AboutSummarySection>
      <AboutSummaryHeader>Awards</AboutSummaryHeader>
      <AboutSummaryItem>
        <ItemLink href="http://www.ece.ucsd.edu/undergraduate/booker-award" target="_blank" rel="noopener noreferrer">UCSD ECE Department</ItemLink> | Booker Award - June 2017
      </AboutSummaryItem>
      <AboutSummaryItem>
        <ItemLink href="https://revelle.ucsd.edu/academics/honors-opportunities/honors-programs.html#Provost-Honors" target="_blank" rel="noopener noreferrer">UCSD Revelle College</ItemLink> | College/Provost Honors - January 2015 - June 2017
      </AboutSummaryItem>
    </AboutSummarySection>
    <Divider />
    <AboutOutroSection>For a detailed summary of my qualifications, please refer to my <ExternalLink href="/DavidLuuResume.pdf" target="_blank" rel="noopener noreferrer">resume</ExternalLink>.</AboutOutroSection>
  </AboutContainer>
);

About.propTypes = propTypes;
About.contextTypes = contextTypes;
About.defaultProps = defaultProps;

export default withCollectRef('About', About);



