import {
  COMPONENT_PADDING_MOBILE,
  COMPONENT_PADDING_DESKTOP,
} from 'styles/theme';

import { withMediaQuery } from 'util/styleHelpers';


export const landingHeaderBaseStyle = font => ({
  margin: 0,
  textTransform: 'uppercase',
  ...font,
});

export const containerStyle = (backgroundColor, textAlign) => withMediaQuery({
  width: '100%',
  backgroundColor,
  textAlign,
}, {
  padding: `${COMPONENT_PADDING_MOBILE} 0`,
}, {
  padding: `${COMPONENT_PADDING_DESKTOP} 0`,
});



