import React from 'react';
import { styled } from 'styletron-react';
import { withMediaQuery } from 'util/styleHelpers';

import {
  BACKGROUND_COLOR_FOOTER,
  FONT_BODY_THIN,
  THEME_COLOR_2,
} from 'styles/theme';

/**
 * Constants
 */

const START_YEAR = 2015;


/**
 * Styled Components
 */

const FooterContainer = styled('footer', withMediaQuery({
  padding: '2em 0',
  backgroundColor: BACKGROUND_COLOR_FOOTER,
  textAlign: 'center',
  ...FONT_BODY_THIN,
  color: '#fff',
}, {
  fontSize: '0.8em',
}, {
  fontSize: '0.9em',
}));

const ExternalLink = styled('a', {
  color: '#fff',
  ':hover': {
    color: THEME_COLOR_2,
  },
});


/**
 * Footer
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const Footer = () => {
  const currentYear = new Date().getFullYear();
  const copyrightYears = currentYear === START_YEAR ? START_YEAR : `${START_YEAR}-${currentYear}`;

  return (
    <FooterContainer>
      Copyright &copy; {copyrightYears} <ExternalLink href="/humans.txt" target="_blank" rel="noopener noreferrer">David Luu</ExternalLink>. Custom designed and built.
    </FooterContainer>
  );
};

Footer.propTypes = propTypes;
Footer.contextTypes = contextTypes;
Footer.defaultProps = defaultProps;

export default Footer;



