import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';
import { pxStringToInt, withMediaQuery } from 'util/styleHelpers.jsx';
import { withCollectRef } from 'util/RefCollector';

import RootContainer from 'components/RootContainer/RootContainer';

import {
  FONT_BODY_NORMAL,
  FONT_SIZE_BASE,
  THEME_COLOR_1,
  THEME_COLOR_2,
  BACKGROUND_COLOR_1,
  COMPONENT_WIDTH_MOBILE,
  COMPONENT_WIDTH_DESKTOP,
} from 'styles/theme';

import { containerStyle } from 'styles/groups';

import Linkedin from './Linkedin.svg';
import LinkedinLight from './Linkedin-light.svg';
import GitHub from './GitHub.svg';
import GitHubLight from './GitHub-light.svg';

/**
 * Constants
 */

const SOCIAL_ICONS = [
  {
    name: 'Linkedin',
    icon: Linkedin,
    iconHover: LinkedinLight,
    href: 'https://www.linkedin.com/in/luudavidc',
  },
  {
    name: 'GitHub',
    icon: GitHub,
    iconHover: GitHubLight,
    href: 'https://github.com/davidcluu',
  },
];


/**
 * Styled Components
 */

const ContactContainer = styled(RootContainer, containerStyle(BACKGROUND_COLOR_1, 'center'));

const Header = styled('h2', {
  margin: '0 0 2em',
  ...FONT_BODY_NORMAL,
  fontSize: '1.75em',
  color: THEME_COLOR_1,
});

const Form = styled('form', {});

const FormGroup = styled('div', withMediaQuery({
  margin: '0 auto 1.5em',
}, {
  width: COMPONENT_WIDTH_MOBILE,
}, {
  width: COMPONENT_WIDTH_DESKTOP,
}));

const FormLabel = styled('label', ({ focus }) => ({
  margin: '0 0.5em 0.5em',
  display: 'block',
  textAlign: 'left',
  fontSize: '1.25em',
  color: focus ? THEME_COLOR_2 : '#000',
}));

const formItemBorderWidth = '2px';
const formItemPadding = '0.75em';

const formItemStyle = {
  border: `${formItemBorderWidth} solid #000`,
  borderRadius: 0,
  padding: formItemPadding,
  width: `calc(95% - 2 * ${formItemBorderWidth} - 2 * ${formItemPadding})`,
  appearance: 'none',
  backgroundColor: BACKGROUND_COLOR_1,
  color: '#000',
  font: 'inherit',
  ':focus': {
    borderColor: THEME_COLOR_2,
    outline: 0,
    color: THEME_COLOR_2,
  },
};

const Input = styled(({ className, style, ariaLabel, name, onFocus, onBlur, componentRef }) => (
  <input name={name} aria-label={ariaLabel} className={className} style={style} autoComplete="off" onFocus={onFocus} onBlur={onBlur} ref={componentRef} />
), formItemStyle);

const Textarea = styled(({ className, style, ariaLabel, name, rows, onFocus, onBlur, componentRef }) => (
  <textarea name={name} aria-label={ariaLabel} className={className} style={style} rows={rows} onFocus={onFocus} onBlur={onBlur} ref={componentRef} />
), formItemStyle);

const SubmitButton = styled('input', Object.assign({}, formItemStyle, {
  padding: `calc(${formItemPadding} / 2)`,
  width: 'auto',
  cursor: 'pointer',
  ':focus': {},
  ':hover': formItemStyle[':focus'],
}));

const Divider = styled('div', {
  ':after': {
    content: '"\\2022\\2022\\2022\\2022\\2022"',
    margin: '3em 0',
    display: 'block',
    fontSize: '.8em',
    textAlign: 'center',
    letterSpacing: '.5em',
    color: '#ccc',
  },
});

const SocialIcons = styled('div', {
  textAlign: 'center',
});

const socialIconMargin = `0 ${0.5 * pxStringToInt(FONT_SIZE_BASE)}px`;
const socialIconSize = `${1.75 * pxStringToInt(FONT_SIZE_BASE)}px`;

const SocialIcon = styled('div', ({ backgroundImage, backgroundImageHover }) => ({
  margin: socialIconMargin,
  width: socialIconSize,
  height: socialIconSize,
  display: 'inline-block',
  backgroundImage: `url(${backgroundImage})`,
  fontSize: 0,
  backgroundSize: '100%',
  ':hover': {
    backgroundImage: `url(${backgroundImageHover})`,
  },
}));


/**
 * Contact
 */

const propTypes = {
  componentRef: PropTypes.func.isRequired,
};

const contextTypes = {};
const defaultProps = {};

class Contact extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      focusedInput: '',
      emailSuccess: false,
    };

    this._onInputFocusGenerator = this.onInputFocusGenerator.bind(this);
    this._onInputBlur = this.onInputBlur.bind(this);
    this._onFormSubmit = this.onFormSubmit.bind(this);
  }

  onInputFocusGenerator(inputName) {
    return () => this.setState({ focusedInput: inputName });
  }

  onInputBlur() {
    this.setState({ focusedInput: '' });
  }

  onFormSubmit(e) {
    e.preventDefault();

    const { nameInput, emailInput, messageInput } = this;
    const name = nameInput.value;
    const email = emailInput.value;
    const message = messageInput.value;

    const xhr = new XMLHttpRequest();
    xhr.open('POST', 'https://formspree.io/luu.david.c@gmail.com');
    xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
    xhr.setRequestHeader('Accept', 'application/json');

    xhr.onreadystatechange = () => {
      if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
        const res = JSON.parse(xhr.response);

        if (res.success) {
          this.setState({
            emailSuccess: true,
          });
        }
      }
    };

    xhr.send(`name=${name}&email=${email}&message=${message}`);
  }

  render() {
    const { componentRef } = this.props;
    const { focusedInput, emailSuccess } = this.state;

    return (
      <ContactContainer componentRef={componentRef}>
        <Header>Get In Touch</Header>
        <Form onSubmit={this._onFormSubmit}>
          <FormGroup>
            <FormLabel htmlFor="name" focus={focusedInput === 'name'}>Name *</FormLabel>
            <Input
              name="name"
              ariaLabel="name"
              onFocus={this._onInputFocusGenerator('name')}
              onBlur={this._onInputBlur}
              componentRef={(el) => { this.nameInput = el; }}
            />
          </FormGroup>
          <FormGroup>
            <FormLabel htmlFor="email" focus={focusedInput === 'email'}>Email *</FormLabel>
            <Input
              name="email"
              ariaLabel="email"
              type="email"
              onFocus={this._onInputFocusGenerator('email')}
              onBlur={this._onInputBlur}
              componentRef={(el) => { this.emailInput = el; }}
            />
          </FormGroup>
          <FormGroup>
            <FormLabel htmlFor="message" focus={focusedInput === 'message'}>Message *</FormLabel>
            <Textarea
              name="message"
              ariaLabel="message"
              rows="7"
              onFocus={this._onInputFocusGenerator('message')}
              onBlur={this._onInputBlur}
              componentRef={(el) => { this.messageInput = el; }}
            />
          </FormGroup>
          {!emailSuccess && <SubmitButton name="submit" type="submit" />}
          {emailSuccess && <div>Hello</div>}
        </Form>
        <Divider />
        <SocialIcons>
          {SOCIAL_ICONS.map(({ name, icon, iconHover, href }) => (
            <a key={href} href={href} target="_blank" rel="noopener noreferrer">
              <SocialIcon backgroundImage={icon} backgroundImageHover={iconHover}>{name}</SocialIcon>
            </a>
          ))}
        </SocialIcons>
      </ContactContainer>
    );
  }
}

Contact.propTypes = propTypes;
Contact.contextTypes = contextTypes;
Contact.defaultProps = defaultProps;

export default withCollectRef('Contact', Contact);



