import {
  MOBILE_MEDIA_QUERY,
  DESKTOP_MEDIA_QUERY,
} from 'styles/theme';

export function minimizeStyleString(style) {
  return style.replace(/\s/g, '');
}

export function classNames(...classes) {
  return classes.filter(conditionalClass => conditionalClass).join(' ');
}

export function pxStringToInt(px) {
  return parseInt(px, 10);
}

export function pxStringToFloat(px) {
  return parseFloat(px);
}

export function hexColorToRgb(color) {
  return {
    r: parseInt(color.substring(1, 3), 16),
    g: parseInt(color.substring(3, 5), 16),
    b: parseInt(color.substring(5, 7), 16),
  };
}

export function rgbToString(color) {
  return `rgb(${color.r}, ${color.g}, ${color.b})`;
}

export function withMediaQuery(baseStyles, mobileStyles, desktopStyles) {
  return {
    ...baseStyles,
    [MOBILE_MEDIA_QUERY]: mobileStyles,
    [DESKTOP_MEDIA_QUERY]: desktopStyles,
  };
}



