import React from 'react';
import PropTypes from 'prop-types';
import withWindowDimensions from './withWindowDimensions';

/**
 * LqipImage
 */

const propTypes = {
  Component: PropTypes.func.isRequired,
  src: PropTypes.string.isRequired,
  lqipSrc: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
  componentRef: PropTypes.func,
  windowHeight: PropTypes.number.isRequired,
};

const contextTypes = {};

const defaultProps = {
  componentRef: () => {},
};

class LqipImage extends React.PureComponent {
  constructor(props) {
    super(props);

    this.componentRef = (el) => {
      this.el = el;
      props.componentRef(el);
    };

    this.state = {
      loaded: false,
    };

    this._onWindowScroll = this.onWindowScroll.bind(this);
  }

  componentDidMount() {
    this._onWindowScroll();
    window.addEventListener('scroll', this._onWindowScroll);
  }

  componentWillUnmount() {
    if (!this.state.loaded) {
      window.removeEventListener('scroll', this._onWindowScroll);
    }
  }

  onWindowScroll() {
    const { windowHeight } = this.props;

    const clientRect = this.el.getBoundingClientRect();

    if (clientRect.top - windowHeight <= 0) {
      const img = new Image();
      img.onload = () => {
        window.removeEventListener('scroll', this._onWindowScroll);
        this.setState({ loaded: true });
      };
      img.src = this.props.src;
    }
  }

  render() {
    const { Component, src, lqipSrc, alt } = this.props;
    const { loaded } = this.state;

    if (loaded) {
      return <Component src={src} alt={alt} componentRef={this.componentRef} />;
    }

    return <Component src={lqipSrc} alt={alt} componentRef={this.componentRef} />;
  }
}

LqipImage.propTypes = propTypes;
LqipImage.contextTypes = contextTypes;
LqipImage.defaultProps = defaultProps;

export default withWindowDimensions(LqipImage);



