import React from 'react';
import { render } from 'react-dom';
import { BrowserRouter } from 'react-router-dom';
import { matchRoutes } from 'react-router-config';
import ReactSegment from 'util/ReactSegment';
import Styletron from 'styletron-client';
import { StyletronProvider } from 'styletron-react';
import { reset } from 'util/RefCollector';
import runtime from 'serviceworker-webpack-plugin/lib/runtime';
import registerEvents from 'serviceworker-webpack-plugin/lib/browser/registerEvents';

import App from './App';
import { staticRoutes } from './Routes';

/**
 * Segment Analytics
 */

ReactSegment.initialize('K4qCf5mmTqkhaCtinfrUHKIc1HRDH9A0', { debug: process.env.NODE_ENV === 'development' });
ReactSegment.page();


/**
 * why-did-you-update in Development
 */

if (process.env.NODE_ENV === 'development') {
  // https://github.com/garbles/why-did-you-update/issues/45
  let createClass = React.createClass; // eslint-disable-line no-unused-vars
  Object.defineProperty(React, 'createClass', {
    set: (nextCreateClass) => {
      createClass = nextCreateClass;
    },
  });

  const { whyDidYouUpdate } = require('why-did-you-update'); // eslint-disable-line global-require
  whyDidYouUpdate(React);
}

/**
 * Render Initial Elements
 */

const mountElement = document.getElementById('root');

function renderInitial(InitialComponent) {
  let rootElement = null;

  if (process.env.NODE_ENV === 'production') {
    const styleElements = document.getElementsByClassName('_styletron_hydrate_');

    rootElement = (
      <StyletronProvider styletron={new Styletron(styleElements)}>
        <BrowserRouter>
          <App InitialComponent={InitialComponent} />
        </BrowserRouter>
      </StyletronProvider>
    );
  } else {
    const { AppContainer } = require('react-hot-loader'); // eslint-disable-line global-require, import/no-extraneous-dependencies

    let styleElements = document.getElementsByClassName('_styletron_hydrate_');

    rootElement = (
      <AppContainer>
        <StyletronProvider styletron={new Styletron(styleElements)}>
          <BrowserRouter>
            <App InitialComponent={InitialComponent} />
          </BrowserRouter>
        </StyletronProvider>
      </AppContainer>
    );

    // Hot reload in development
    if (module.hot) {
      module.hot.accept('./App', () => {
        const NextApp = require('./App').default; // eslint-disable-line global-require

        reset();

        styleElements = document.getElementsByClassName('_styletron_hydrate_');

        render(
          <AppContainer>
            <StyletronProvider styletron={new Styletron(styleElements)}>
              <BrowserRouter>
                <NextApp InitialComponent={false} />
              </BrowserRouter>
            </StyletronProvider>
          </AppContainer>,
          mountElement
        );
      });
    }
  }

  render(
    rootElement,
    mountElement
  );
}

const { getComponent } = matchRoutes(staticRoutes, window.location.pathname)[0].route;

if (getComponent) {
  getComponent()
    .then(module => module.default || module)
    .then(Component => renderInitial(Component));
} else {
  renderInitial(false);
}


/*
 * Service Worker
 */

const swEnabledDev = true;

if (
  // Enable toggle
  (process.env.NODE_ENV === 'production' || swEnabledDev) &&
  // SW support
  'serviceWorker' in navigator &&
  // HTTPS or localhost
  (window.location.protocol === 'https:' || window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1')
) {
  window.addEventListener('load', () => {
    const registration = runtime.register({
      scope: '/',
    });

    registerEvents(registration, {
      // No SW when page loaded + SW 'activated' state
      onInstalled: () => {
        // console.log('onInstalled');

        // SW controller ready here
      },

      // Had SW when page loaded + SW 'installed' state
      onUpdateReady: () => {
        // console.log('onUpdateReady');
      },
      // Had SW when page loaded + SW 'installing' state
      onUpdating: () => {
        // console.log('onUpdating');
      },
      // Had SW when page loaded + SW 'redundant' state
      onUpdateFailed: () => {
        // console.log('onUpdateFailed');
      },
      // Had SW when page loaded + SW 'activated' state
      onUpdated: () => {
        // console.log('onUpdated');
      },
    });
  });
}



