/* eslint-disable max-len */

import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';

import LqipImage from 'util/lqipImage';

import {
  FONT_SIZE_BASE,
  THEME_COLOR_2,
} from 'styles/theme';

let TimelineLite = {};
if (process.env.BROWSER) {
  TimelineLite = require('gsap/TimelineLite'); // eslint-disable-line global-require
}

/**
 * Styled Components
 */

const ProjectAssetContainer = styled('div', {
  position: 'relative',
  overflow: 'hidden',
  fontSize: 0,
});

const ProjectImage = styled(({ src, alt, className, componentRef }) => (
  <img className={className} src={src} alt={alt} ref={componentRef} />
), {
  width: '100%',
});

const borderWidth = '2px';
const paddingLr = '5%';
const ProjectDescriptionContainer = styled(({ className, componentRef, children }) => (
  <div className={className} ref={componentRef}>
    {children}
  </div>
), {
  border: `${borderWidth} solid #000`,
  borderRadius: '.5em',
  padding: `0 ${paddingLr}`,
  width: `calc(100% - 2 * ${paddingLr} - 2 * ${borderWidth})`,
  height: `calc(100% - 2 * ${borderWidth})`,
  position: 'absolute',
  display: 'flex',
  alignItems: 'center',
  top: 0,
  fontSize: FONT_SIZE_BASE,
  // Animation Base
  opacity: 0,
  transform: 'scaleX(0.9) scaleY(0.9)',
});

const ProjectDescription = styled('div', {
});

const ProjectLinks = styled('ul', {
  marginTop: '1.5em',
  padding: 0,
  listStyle: 'none',
});

const ProjectLinkContainer = styled('li', {
  margin: '0 0.5em',
  display: 'inline-block',
});

const ProjectLink = styled('a', {
  border: '2px solid #000',
  borderRadius: '.5em',
  padding: '0.5em 1em',
  width: '60px',
  display: 'inline-block',
  textDecoration: 'none',
  color: '#000',
  ':hover': {
    borderColor: THEME_COLOR_2,
    color: THEME_COLOR_2,
  },
});


/**
 * Projects
 */

const propTypes = {
  name: PropTypes.string.isRequired,
  lqImg: PropTypes.string.isRequired,
  img: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  links: PropTypes.arrayOf(
    PropTypes.shape({
      href: PropTypes.string.isRequired,
      text: PropTypes.string.isRequired,
    })
  ),
};

const contextTypes = {};

const defaultProps = {
  links: [],
};

class ProjectAsset extends React.Component {
  static ANIMATION_DURATION = 0.25;

  _animate = animateIn => () => {
    if (this.image && this.description) {
      new TimelineLite()
        .to(this.image, ProjectAsset.ANIMATION_DURATION, {
          opacity: animateIn ? 0 : 1,
          scaleX: animateIn ? 1.1 : 1,
          scaleY: animateIn ? 1.1 : 1,
        })
        .to(this.description, ProjectAsset.ANIMATION_DURATION, {
          opacity: animateIn ? 1 : 0,
          scaleX: animateIn ? 1 : 0.9,
          scaleY: animateIn ? 1 : 0.9,
        }, 0);
    }
  }

  _onMouseEnter = this._animate(true)
  _onMouseLeave = this._animate(false)

  render() {
    const { name, lqImg, img, description, links } = this.props;

    return (
      <ProjectAssetContainer onMouseEnter={this._onMouseEnter} onMouseLeave={this._onMouseLeave}>
        <LqipImage Component={ProjectImage} src={img} lqipSrc={lqImg} alt={name} componentRef={(el) => { this.image = el; }} />
        <ProjectDescriptionContainer componentRef={(el) => { this.description = el; }}>
          <div>
            <ProjectDescription>{description}</ProjectDescription>
            {links.length > 0 && <ProjectLinks>
              {links.map(({ href, text }) => (
                <ProjectLinkContainer key={text}>
                  <ProjectLink href={href} target="_blank" rel="noopener noreferrer">{text}</ProjectLink>
                </ProjectLinkContainer>
              ))}
            </ProjectLinks>}
          </div>
        </ProjectDescriptionContainer>
      </ProjectAssetContainer>
    );
  }
}

ProjectAsset.propTypes = propTypes;
ProjectAsset.contextTypes = contextTypes;
ProjectAsset.defaultProps = defaultProps;

export default ProjectAsset;



