import React from 'react';
import PropTypes from 'prop-types';
import ReactSegment from 'util/ReactSegment';
import { styled } from 'styletron-react';
import { pxStringToInt, withMediaQuery } from 'util/styleHelpers';
import { chainHOC } from 'util/hocHelpers';
import { withProvideRefs } from 'util/RefCollector';
import withWindowDimensions from 'util/withWindowDimensions';

import {
  MOBILE_MAX_WIDTH as mobileMaxWidth,
  DESKTOP_MIN_WIDTH as desktopMinWidth,
  NAVBAR_HEIGHT_DESKTOP as navbarHeightDesktop,
  NAVBAR_HEIGHT_MOBILE as navbarHeightMobile,
  FONT_BODY_NORMAL,
  FONT_SIZE_BASE,
  NAVBAR_COLOR,
  THEME_COLOR_1,
  THEME_COLOR_2,
} from 'styles/theme';

let TweenLite = {};
let TimelineLite = {};
if (process.env.BROWSER) {
  TweenLite = require('gsap/TweenLite'); // eslint-disable-line global-require
  TimelineLite = require('gsap/TimelineLite'); // eslint-disable-line global-require
  require('gsap/CSSPlugin'); // eslint-disable-line global-require
  require('gsap/ScrollToPlugin'); // eslint-disable-line global-require
}

/**
 * Constants
 */

const DESKTOP_MIN_WIDTH = pxStringToInt(desktopMinWidth);
const MOBILE_MAX_WIDTH = pxStringToInt(mobileMaxWidth);
const NAVBAR_HEIGHT_DESKTOP = pxStringToInt(navbarHeightDesktop);
const NAVBAR_HEIGHT_MOBILE = pxStringToInt(navbarHeightMobile);

const NAVBAR_KEYS = [
  'About',
  'Projects',
  'Contact',
];

const EXTERNAL_LINKS = [
  {
    name: 'Resume',
    href: '/DavidLuuResume.pdf',
  },
];


/**
 * Styled Components
 */

const NavbarContainer = styled('section', ({ mounted, isScrolledPastLanding }) => withMediaQuery({
  width: '100%',
  position: 'fixed',
  zIndex: 9999,
  backgroundColor: isScrolledPastLanding ? THEME_COLOR_1 : NAVBAR_COLOR,
  transition: mounted ? 'background-color 0.5s' : 'none',
}, {
  height: navbarHeightMobile,
  textAlign: 'right',
}, {
  height: navbarHeightDesktop,
}));

const mobileMenuWidth = 30;
const mobileMenuHeight = 25;
const mobileMenuMargin = (pxStringToInt(navbarHeightMobile) - mobileMenuHeight) / 2;
const burgerHeight = 5;
const NAV_GOAL = (mobileMenuHeight / 2) - (burgerHeight / 2);

const MobileMenu = styled(({ className, style, ariaLabel, onClick, children }) => (
  <button className={className} style={style} aria-label={ariaLabel} onClick={onClick}>
    {children}
  </button>
), withMediaQuery({}, {
  margin: `${mobileMenuMargin}px`,
  border: 0,
  padding: 0,
  width: `${mobileMenuWidth}px`,
  height: `${mobileMenuHeight}px`,
  display: 'inline-block',
  appearance: 'none',
  outline: 'none',
  backgroundColor: 'transparent',
  fontSize: 0,
}, {
  display: 'none',
}));

const Burger = styled(({ className, style, componentRef, children }) => (
  <div className={className} style={style} ref={componentRef}>
    {children}
  </div>
), ({ mounted, isScrolledPastLanding }) => withMediaQuery({}, {
  width: `${mobileMenuWidth}px`,
  height: `${burgerHeight}px`,
  position: 'absolute',
  backgroundColor: isScrolledPastLanding ? NAVBAR_COLOR : THEME_COLOR_1,
  transition: mounted ? 'background-color 0.5s' : 'none',
}, {}));

const BurgerBase = styled(({ className, style, isScrolledPastLanding, componentRef, children }) => (
  <Burger className={className} style={style} isScrolledPastLanding={isScrolledPastLanding} componentRef={componentRef}>
    {children}
  </Burger>
), {});

const Burger1 = styled(BurgerBase, {
  top: `${mobileMenuMargin}px`,
});

const Burger2 = styled(BurgerBase, {
  top: `${(mobileMenuMargin + (mobileMenuHeight / 2)) - (burgerHeight / 2)}px`,
});

const Burger3 = styled(BurgerBase, {
  bottom: `${mobileMenuMargin}px`,
});

const Nav = styled(({ className, style, componentRef, children }) => (
  <div className={className} style={style} ref={componentRef}>
    {children}
  </div>
), withMediaQuery({
  position: 'absolute',
  fontSize: 0,
}, {
  width: '100%',
  top: navbarHeightMobile,
  overflow: 'hidden',
  // Animation Base
  visibility: 'hidden',
}, {
  width: '100%',
  height: '100%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
}));

const NavButton = styled('button', ({ mounted, isScrolledPastLanding, isCurrentlyVisibleSection }) => withMediaQuery({
  border: 0,
  padding: 0,
  outline: 'none',
  cursor: 'pointer',
  backgroundColor: 'transparent',
  textTransform: 'uppercase',
  ...FONT_BODY_NORMAL,
  color: isScrolledPastLanding ? (isCurrentlyVisibleSection ? THEME_COLOR_2 : '#fff') : '#000', // eslint-disable-line no-nested-ternary
  fontSize: FONT_SIZE_BASE,
}, {
  margin: 0,
  padding: '1em 0',
  width: '100%',
  display: 'inline-block',
  transition: mounted ? 'background-color 0.5s' : 'none',
  backgroundColor: isScrolledPastLanding ? THEME_COLOR_1 : NAVBAR_COLOR,
}, {
  margin: '0 1em',
}));

const NavLink = styled('a', ({ mounted, isScrolledPastLanding, isCurrentlyVisibleSection }) => withMediaQuery({
  textTransform: 'uppercase',
  textDecoration: 'none',
  ...FONT_BODY_NORMAL,
  color: isScrolledPastLanding ? (isCurrentlyVisibleSection ? THEME_COLOR_2 : '#fff') : '#000', // eslint-disable-line no-nested-ternary
  fontSize: FONT_SIZE_BASE,
}, {
  padding: '1em 0',
  width: '100%',
  display: 'inline-block',
  transition: mounted ? 'background-color 0.5s' : 'none',
  backgroundColor: isScrolledPastLanding ? THEME_COLOR_1 : NAVBAR_COLOR,
  textAlign: 'center',
}, {
  margin: '0 1em',
}));


/**
 * Navbar
 */

const propTypes = {
  refs: PropTypes.object.isRequired,
  windowWidth: PropTypes.number.isRequired,
  windowHeight: PropTypes.number.isRequired,
};

const contextTypes = {};
const defaultProps = {};

class Navbar extends React.PureComponent {
  static SCROLL_PX_PER_MS = 1000;
  static ANIMATION_DURATION = 0.5;

  constructor(props) {
    super(props);

    this.state = {
      mounted: false,
      mobileNavOpen: false,
      isScrolledPastLanding: false,
      currentlyVisibleSection: '',
    };

    this._onWindowScroll = this.onWindowScroll.bind(this);
    this._onMobileMenuClick = this.onMobileMenuClick.bind(this);
    this._onNavbarItemClickGenerator = this.onNavbarItemClickGenerator.bind(this);

    this._onNavbarItemClickHandlers = {};
    NAVBAR_KEYS.forEach((key) => {
      this._onNavbarItemClickHandlers[key] = this._onNavbarItemClickGenerator(key);
    });

    this._burgerRefGenerator = this.burgerRefGenerator.bind(this);

    [1, 2, 3].forEach((i) => {
      this[`_burger${i}Ref`] = this._burgerRefGenerator(i).bind(this);
    });
  }

  componentDidMount() {
    const { nav } = this;
    const { windowWidth } = this.props;

    // Set up Nav for menu animations
    if (windowWidth >= DESKTOP_MIN_WIDTH) {
      nav.style.height = '';
    } else {
      this.navHeight = this.navHeight || nav.clientHeight;
      nav.style.height = 0;
      nav.style.visibility = 'visible';
    }

    this._onWindowScroll();
    window.addEventListener('scroll', this._onWindowScroll);

    this.setState({
      mounted: true,
    });
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this._onWindowScroll);
  }

  onWindowScroll() {
    const { isScrolledPastLanding, currentlyVisibleSection } = this.state;
    const { refs, windowWidth, windowHeight } = this.props;
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

    let scrollOffset = 0;
    if (windowWidth >= DESKTOP_MIN_WIDTH) {
      scrollOffset = NAVBAR_HEIGHT_DESKTOP;
    } else {
      scrollOffset = NAVBAR_HEIGHT_MOBILE;
    }

    const isNowScrolledPastLanding = scrollTop + scrollOffset >= windowHeight;

    let newCurrentElement = '';
    if (isNowScrolledPastLanding) {
      NAVBAR_KEYS.forEach((key) => {
        const element = refs[key];

        if (element && (element.offsetTop - scrollOffset) - scrollTop <= 0) {
          newCurrentElement = key;
        }
      });
    }

    if (isScrolledPastLanding !== isNowScrolledPastLanding || currentlyVisibleSection !== newCurrentElement) {
      this.setState({
        isScrolledPastLanding: isNowScrolledPastLanding,
        currentlyVisibleSection: newCurrentElement,
      });
    }
  }

  onMobileMenuClick() {
    const { mobileNavOpen } = this.state;

    new TimelineLite()
      .to(this.nav, Navbar.ANIMATION_DURATION, { height: mobileNavOpen ? 0 : this.navHeight })
      .to(this.burger1, Navbar.ANIMATION_DURATION, {
        y: mobileNavOpen ? 0 : NAV_GOAL,
        rotation: mobileNavOpen ? 0 : '-45deg',
      }, 0)
      .to(this.burger2, Navbar.ANIMATION_DURATION, { opacity: mobileNavOpen ? 1 : 0 }, 0)
      .to(this.burger3, Navbar.ANIMATION_DURATION, {
        y: mobileNavOpen ? 0 : -NAV_GOAL,
        rotation: mobileNavOpen ? 0 : '45deg',
      }, 0);

    this.setState({
      mobileNavOpen: !mobileNavOpen,
    });
  }

  onNavbarItemClickGenerator(key) {
    return () => {
      ReactSegment.track('Navbar Item Clicked', {
        item: key,
      });

      const { refs, windowWidth } = this.props;
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

      let scrollOffset = 0;
      if (windowWidth >= DESKTOP_MIN_WIDTH) {
        scrollOffset = NAVBAR_HEIGHT_DESKTOP;
      } else {
        scrollOffset = NAVBAR_HEIGHT_MOBILE;
      }

      const element = refs[key];

      if (element) {
        const scrollTarget = (element.offsetTop - scrollOffset) + 1;
        const scrollDuration = Math.abs(scrollTop - scrollTarget) / Navbar.SCROLL_PX_PER_MS;

        TweenLite.to(window, scrollDuration, { scrollTo: scrollTarget });

        if (windowWidth < MOBILE_MAX_WIDTH) {
          this._onMobileMenuClick();
        }
      }
    };
  }

  burgerRefGenerator(i) {
    return (el) => {
      this[`burger${i}`] = el;
    };
  }

  render() {
    const { mounted, isScrolledPastLanding, currentlyVisibleSection } = this.state;

    return (
      <NavbarContainer mounted={mounted} isScrolledPastLanding={isScrolledPastLanding}>
        <MobileMenu ariaLabel={'Toggle Mobile Navbar'} onClick={this._onMobileMenuClick}>
          <Burger1 isScrolledPastLanding={isScrolledPastLanding} componentRef={this._burger1Ref} />
          <Burger2 isScrolledPastLanding={isScrolledPastLanding} componentRef={this._burger2Ref} />
          <Burger3 isScrolledPastLanding={isScrolledPastLanding} componentRef={this._burger3Ref} />
        </MobileMenu>
        <Nav componentRef={(el) => { this.nav = el; }}>
          {NAVBAR_KEYS.map(key => (
            <NavButton
              key={key}
              mounted={mounted}
              isScrolledPastLanding={isScrolledPastLanding}
              isCurrentlyVisibleSection={currentlyVisibleSection === key}
              onClick={this._onNavbarItemClickHandlers[key]}
            >
              {key}
            </NavButton>
          ))}
          {EXTERNAL_LINKS.map(({ name, href }) => (
            <NavLink
              key={name}
              mounted={mounted}
              isScrolledPastLanding={isScrolledPastLanding}
              isCurrentlyVisibleSection={currentlyVisibleSection === name}
              href={href}
              target="_blank"
              rel="noopener noreferrer"
            >
              {name}
            </NavLink>
          ))}
        </Nav>
      </NavbarContainer>
    );
  }
}

Navbar.propTypes = propTypes;
Navbar.contextTypes = contextTypes;
Navbar.defaultProps = defaultProps;

export default chainHOC(
  withProvideRefs,
  withWindowDimensions,
)(Navbar);



