import React from 'react';
import Helmet from 'react-helmet';
import { minimizeStyleString } from 'util/styleHelpers';

import {
  FONT_BODY_NORMAL,
  FONT_SIZE_BASE,
} from 'styles/theme';

import Routes from 'Routes';

/**
 * App
 */

const propTypes = {};
const contextTypes = {};
const defaultProps = {};

const App = props => (
  <div>
    <Helmet>
      <html lang="en" />
      <meta charSet="utf-8" />
      <meta name="viewport" content="width=device-width,height=device-height,initial-scale=1" />
      <meta content="ie=edge" httpEquiv="x-ua-compatible" />
      {/* Generic SEO */}
      <meta name="author" content="David Luu" />
      <meta name="keywords" content="David Luu, David, Luu, UCSD, Portfolio, Profile, Resume" />
      {/* http://www.robotstxt.org/ */}
      <meta name="robots" content="index, follow" />
      <meta name="revisit-after" content="1 days" />
      {/* http://humanstxt.org/ */}
      <link rel="author" href="./humans.txt" />
      {/* https://support.google.com/webmasters/answer/139066?hl=en */}
      <link rel="canonical" href="https://davidluu.me" />
      {/* https://realfavicongenerator.net */}
      <link rel="apple-touch-icon" sizes="180x180" href="/apple-touch-icon.png?v=dLBM8meaJ9" />
      <link rel="icon" type="image/png" sizes="32x32" href="/favicon-32x32.png?v=dLBM8meaJ9" />
      <link rel="icon" type="image/png" sizes="16x16" href="/favicon-16x16.png?v=dLBM8meaJ9" />
      <link rel="manifest" href="/manifest.json?v=dLBM8meaJ9" />
      <link rel="mask-icon" href="/safari-pinned-tab.svg?v=dLBM8meaJ9" color="#5bbad5" />
      <link rel="shortcut icon" href="/favicon.ico?v=dLBM8meaJ9" />
      <meta name="apple-mobile-web-app-title" content="David Luu - Software Engineer, Web Developer" />
      <meta name="application-name" content="David Luu - Software Engineer, Web Developer" />
      <meta name="theme-color" content="#ffffff" />
      {/* Global Styles */}
      <style type="text/css">{minimizeStyleString(`
        html, body {
          margin: 0;
          padding: 0;
          width: 100%;
          height: 100%;

          position: relative;

          font-size: ${FONT_SIZE_BASE};
          ${Object.keys(FONT_BODY_NORMAL).map(key => `${key.replace(/([a-z])([A-Z])/g, '$1-$2').toLowerCase()}:${FONT_BODY_NORMAL[key]}`).join(';')}
        }

        div[data-reactmount],
        div[data-reactroot] {
          width: 100%;
          height: 100%;
        }
      `)}</style>
    </Helmet>
    <Routes {...props} />
  </div>
);

App.propTypes = propTypes;
App.contextTypes = contextTypes;
App.defaultProps = defaultProps;

export default App;



