/* eslint-disable max-len */

import React from 'react';
import PropTypes from 'prop-types';
import { styled } from 'styletron-react';
import { pxStringToInt, withMediaQuery } from 'util/styleHelpers';
import { withCollectRef } from 'util/RefCollector';

import RootContainer from 'components/RootContainer/RootContainer';

import {
  BACKGROUND_COLOR_2,
  FONT_BODY_NORMAL,
  THEME_COLOR_1,
  COMPONENT_WIDTH_MOBILE,
  COMPONENT_WIDTH_DESKTOP,
} from 'styles/theme';

import { containerStyle } from 'styles/groups';

import ProjectAsset from './components/ProjectAsset/ProjectAsset';

import projects from './data/Projects';

/*
 * Constants
 */

const PROJECTS = projects.map((project) => {
  const lqImg = require(`./${project.lqImgFilename}`); // eslint-disable-line global-require, import/no-dynamic-require
  const img = require(`./${project.imgFilename}`); // eslint-disable-line global-require, import/no-dynamic-require

  return Object.assign({}, project, {
    lqImg,
    img,
  });
});


/**
 * Styled Components
 */

const ProjectsContainer = styled(RootContainer, containerStyle(BACKGROUND_COLOR_2, 'center'));

const Inner = styled('div', withMediaQuery({
  margin: '0 auto',
}, {
  width: COMPONENT_WIDTH_MOBILE,
}, {
  minWidth: COMPONENT_WIDTH_DESKTOP,
  maxWidth: `${pxStringToInt(COMPONENT_WIDTH_DESKTOP) * 1.2}px`,
}));

const Header = styled('h2', {
  margin: '0 0 2em',
  ...FONT_BODY_NORMAL,
  fontSize: '1.75em',
  color: THEME_COLOR_1,
});

const ProjectList = styled('ul', {
  margin: 0,
  padding: 0,
  listStyle: 'none',
  textAlign: 'left',
});

const marginLr = 2.5;
const ProjectItem = styled('li', withMediaQuery({
  display: 'inline-block',
  textAlign: 'center',
}, {
  margin: `0 ${marginLr}% 5%`,
  width: `${100 - (2 * marginLr)}%`,
  position: 'relative',
  ':nth-last-child(-n+1)': {
    marginBottom: 0,
  },
}, {
  margin: `0 ${marginLr}% 5%`,
  width: `${50 - marginLr}%`,
  position: 'relative',
  ':nth-child(2n)': {
    marginRight: 0,
  },
  ':nth-child(2n+1)': {
    marginLeft: 0,
  },
  ':nth-last-child(-n+2)': {
    marginBottom: 0,
  },
}));

const ProjectName = styled('h3', {
  margin: '0.5em 0 0',
  ...FONT_BODY_NORMAL,
  fontSize: '1.25em',
});

const ProjectDates = styled('div', {
  margin: '0.25em 0 0',
  fontSize: '1.1em',
});


/**
 * Projects
 */

const propTypes = {
  componentRef: PropTypes.func.isRequired,
};

const contextTypes = {};
const defaultProps = {};

const Projects = ({ componentRef }) => (
  <ProjectsContainer componentRef={componentRef}>
    <Inner>
      <Header>Projects</Header>
      <ProjectList>
        {PROJECTS.map(({ name, lqImg, img, description, links, dates }) => (
          <ProjectItem key={name}>
            <ProjectAsset
              name={name}
              lqImg={lqImg}
              img={img}
              description={description}
              links={links}
            />
            <ProjectName>{name}</ProjectName>
            <ProjectDates>{dates}</ProjectDates>
          </ProjectItem>
        ))}
      </ProjectList>
    </Inner>
  </ProjectsContainer>
);

Projects.propTypes = propTypes;
Projects.contextTypes = contextTypes;
Projects.defaultProps = defaultProps;

export default withCollectRef('Projects', Projects);



