import React from 'react'; // eslint-disable-line
import PropTypes from 'prop-types';
import { matchRoutes, renderRoutes } from 'react-router-config';
import asyncRoute from 'util/asyncRoute';

/**
 * Routes
 */

const Index = require('modules/Index/Index').default;

const getResume = () => import(/* webpackChunkName: "Resume" */ 'modules/Resume/Resume');
const Resume = asyncRoute(getResume, 'Resume');

const getNotFound = () => import(/* webpackChunkName: "NotFound" */ 'modules/NotFound/NotFound');
const NotFound = asyncRoute(getNotFound, 'NotFound');

export const staticRoutes = [
  {
    path: '/',
    component: Index,
    exact: true,
  },
  {
    path: '/resume',
    component: Resume,
    getComponent: getResume,
    exact: true,
  },
  {
    component: NotFound,
    getComponent: getNotFound,
  },
];


/**
 * Routes
 */

let propTypes = {
  InitialComponent: PropTypes.any.isRequired,
};
if (!process.env.BROWSER) {
  propTypes = {};
}

const contextTypes = {};
const defaultProps = {};

class Routes extends React.PureComponent {
  constructor(props) {
    super(props);

    let routes = staticRoutes;

    // Only modify routes on client-side
    if (process.env.BROWSER) {
      const { InitialComponent } = props;
      const initialRoute = matchRoutes(staticRoutes, window.location.pathname)[0].route;

      // Swap out initial async component with preloaded component if it is passed as a prop
      if (InitialComponent) {
        routes = staticRoutes.map((route) => {
          const modifiedRoute = Object.assign({}, route);

          if (route.path === initialRoute.path) {
            modifiedRoute.component = InitialComponent;
          }

          return modifiedRoute;
        });
      }
    }

    this.routes = routes;
  }

  render() {
    return renderRoutes(this.routes);
  }
}

Routes.propTypes = propTypes;
Routes.contextTypes = contextTypes;
Routes.defaultProps = defaultProps;

export default Routes;



